.searchForm {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  margin: 0.25rem 0 0;
  padding: 0 0.5rem 0.25rem;
  border-bottom: 1px solid #eee;
  label {
    position: absolute;
    left: -10000px;
  }
  input[type="text"] {
    flex-grow: 1;
    max-width: 100%;
    min-height: 48px;
    padding: 0.25rem;
  }
  input::placeholder {
    font-size: 1rem;
  }
}
