main {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  overflow-y: auto;
  flex-direction: column;
  flex-grow: 1;

  ul {
    width: 100%;
    list-style: none;
    padding: 0 0.25rem 0.25rem;

    li::before {
      content: "\200B";
    }

    .item {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      padding: 0.5rem 0 0.5rem 0.5rem;
      margin: 0.25rem 0;
      background-color: #eee;

      input[type="checkbox"] {
        text-align: center;
        width: 2.5rem;
        width: 48px;
        min-width: 48px;
        height: 2.5rem;
        height: 48px;
        min-height: 48px;
        cursor: pointer;
        margin-right: 0.5rem;
      }
      input[type="checkbox"]:focus + label {
        text-decoration: underline;
      }
      label {
        font-size: 0.75rem;
        flex-grow: 1;
      }
      svg {
        width: 48px;
        min-width: 48px;
        height: 36px;
        font-size: 1rem;
        color: steelblue;
        cursor: pointer;
      }
      svg:focus,
      svg:hover {
        color: red;
        outline: none;
      }
    }
    .item:first-child {
      margin: 0;
    }
  }
  p {
    height: 100%;
    font-weight: 700;

    display: flex;
    align-items: center;
  }
}
